@import '~assets/styles/h3Imports';

.root {
  display: flex;
  justify-content: center;

  --button-min-width: #{em(248, 18)};

  @media (max-width: $max-tablet-size) {
    --button-min-width: #{em(208, 18)};
  }

  @media (max-width: $max-phone-size) {
    --button-min-width: 100%;
  }

  .buttons {
    display: grid;
    grid-gap: em(16);
    padding: em(4);
    width: 100%;

    @media #{$tablet-up} {
      width: auto;

      &:not(.single) {
        grid-template-columns: 1fr 1fr;

        &:first-child {
          grid-column-end: auto;
        }
      }
    }
  }

  .reversed {
    display: grid;
    grid-gap: em(16);
    width: 100%;
  }
}
