@import '~assets/styles/h3Imports';

.root {
  --modal-body-image-height: #{em(210)};

  &[data-modal-section='image'] {
    display: flex;
    height: var(--modal-body-image-height);
    justify-content: center;
    margin-top: var(--spacing-m);

    @media #{$tablet-up} {
      margin-top: var(--spacing-s);
    }
  }
}
