@import '~assets/styles/h3Imports';

.root {
  --modal-body-max-height: #{em(400)};

  overflow-y: auto;

  [data-modal-section='header'] {
    padding-right: em(70);
  }

  &[data-modal-section='body'] {
    &.centered {
      display: flex;
      flex-direction: column;
      text-align: center;

      [data-modal-section='header'] {
        margin-left: auto;
        margin-right: auto;
      }
    }

    .overflow {
      display: block;
      max-height: var(--modal-body-max-height);
      overflow: auto;
    }

    &.with-image {
      [data-modal-section='header'] {
        margin-bottom: var(--spacing-xs);
        margin-top: var(--spacing-m);
        padding-right: 0;

        @media (max-width: $max-tablet-size) {
          margin-top: var(--spacing-s);
        }

        @media (max-width: $max-phone-size) {
          margin-top: var(--spacing-xs);
        }
      }
    }
  }
}
