@import '~assets/styles/h3Imports';

.root {
  align-items: center;
  display: flex;
  margin-bottom: em(32);
  text-align: start;

  > :not(.icon) {
    margin-bottom: 0;
    margin-top: 0;
  }

  .icon {
    margin: em(-6) em(24) 0 0;

    @media (max-width: $max-tablet-size) {
      margin-right: em(16);
    }
  }

  .image {
    display: flex;
    justify-content: center;
  }
}
