@import '~assets/styles/h3Imports';

.root {
  --modal-spacing-desktop: var(--spacing-l);
  --modal-spacing-tablet: var(--spacing-m);
  --modal-spacing-mobile: var(--spacing-xs);
  --modal-backgroung-color: var(--white);
  --modal-border-radius: #{em(24)};
  --modal-max-width: #{em(800)};

  background: var(--modal-backgroung-color);
  border-radius: var(--modal-border-radius);
  box-shadow: var(--box-shadow-modal);
  margin: auto;
  max-height: 100vh;
  max-width: var(--modal-max-width);
  min-width: $minimal-screen-width;
  overflow-y: auto;
  padding: var(--modal-spacing-desktop);
  position: relative;
  top: 50%;
  transform: translateY(-50%);

  @media (max-width: $max-tablet-size) {
    --modal-max-width: #{em(554)};

    padding: var(--modal-spacing-tablet);
  }

  @media (max-width: $max-phone-size) {
    --modal-max-width: 100%;

    margin-left: #{em(16)};
    margin-right: #{em(16)};
    padding: var(--modal-spacing-mobile);
  }

  @media (max-height: 450px) {
    height: 100%;
  }

  @media (max-width: 424px) {
    min-width: unset;
  }
}

.close-button {
  --button-tiny-padding: #{em(6, 16)};
  --button-tiny-font-size: #{em(16)};

  align-items: center;
  display: flex;
  justify-content: space-between;
  position: absolute;
  right: calc(var(--modal-spacing-desktop) - var(--button-tiny-padding));
  top: calc(var(--modal-spacing-desktop) - var(--button-tiny-padding));

  &-text {
    @include interactive-text;

    line-height: 1;
  }

  @media (max-width: $max-tablet-size) {
    right: calc(var(--modal-spacing-tablet) - var(--button-tiny-padding));
    top: calc(var(--modal-spacing-tablet) - var(--button-tiny-padding));
  }

  @media (max-width: $max-phone-size) {
    right: calc(var(--modal-spacing-mobile) - var(--button-tiny-padding));
    top: calc(var(--modal-spacing-mobile) - var(--button-tiny-padding));
  }
}

.overlay {
  background: var(--modal-overlay-background);
  height: 100%;
  left: 0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;

  @media (max-width: $max-tablet-size) {
    --modal-spacing: var(--modal-spacing-tablet);
  }

  @media (max-width: $max-phone-size) {
    --modal-spacing: var(--modal-spacing-mobile);
    --buttons-padding-small: #{em(14)} #{em(15)};
    --bordered-buttons-padding-small: #{em(13)} #{em(16)};
  }
}
